<template>
<div class="center">
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Logistics</h1>
      <div class="chartWrap">
        <a @click="$router.push('/cart')" class="iconfont back"> &#xe669; </a></div>
    </section>
    <div class="centent">
        <div class="information">
            <div class="coat">Order No: <span>{{logistic.orderNo}}</span></div>
            <div class="coat">Logistics Number: <span>{{logistic.expressNo}}</span></div>
            <div class="coat">Courier Services Company: <span>{{logistic.expressName}}</span></div>
            <div class="coat">Estimated Delivered Time: <span>{{logistic.expectedDeliveryTime}}</span></div>
        </div>
        <div class="steps">
          <ul>
            <li v-for="(item,index) in logistic.logisticsInformationList">
              <div class="content">
                <span class="circle"> </span>
                <div class="timebar">
                  <span> {{item.date.split(' ')[0].substring(5,10)}} </span> <br/>
                  <span style="font-size: 11px;"> {{item.date.split(' ')[1].substring(0,5)}}</span>
                </div>
                 <p class="txt"> <span> {{'['+item.location+'] '}}{{item.remark}}</span> </p>
              </div>
            </li>
          </ul>
        </div>

    </div>
</div>
</template>

<script>
import {getLogisticsInformation} from '@/api/order'

export default {
  name: 'logistics',
  data () {
    return {
     value:'',
     orderNo:this.$route.query.orderNo,
     logistic:{}
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        getLogisticsInformation({orderNo:this.orderNo}).then(res=>{
            this.logistic=res.data.result;
        });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    }


  }
}
</script>
<style lang="scss">

    .steps{
        ul{ padding:30px 15px 0 60px;
          li{ border-left: solid 1px #ccc;color:#999; padding-left:15px;
            .content{min-height: 63px; position: relative;
              .txt{height: 50px; display: flex; align-items: center;position: relative; top:-25px;}
              .circle{border-radius: 100%; display: block; width:24px; height: 24px; background-color: #ccc; position:absolute; left: -27px; top:0; margin-top: -12px; border:solid 5px #fff;}
              .timebar{position: absolute; left: -65px; top:0; margin-top:-20px;
                span{
                  font-size: 13px;
                }
              }
            }
          }
          li:first-child{ color:#333;
            .circle{background-color: #f4b4b9; width:30px; height: 30px; margin-top: -15px; left: -31px; text-align:center; line-height:20px;
              &::after{ content: "\e63e";font-family: "iconfont"; background: #eb5555; border-radius: 100%; color:#ffffff; font-size: 12px;display: inline-block; padding: 1px; height: 14px; width:14px; line-height: 14px;}
            }

          }
          li:last-child{border:none;}
        }
    }
</style>
<style lang="scss" scoped>
    .center{
        width: 100%;
        height: 100%;
        background-color: #f6f6f6;
        position: fixed;
        .head{
            background-color: #fff;
        }
        .centent{
            width: 100%;
            height: 100%;
            .information{
                width: 92%;
                margin: 15px 4%;
                background-color: #fff;
                padding: 15px 0 25px 15px;
                border-radius: 10px;
                    -webkit-box-shadow: 0 2px 5px 2px rgba(185, 185, 185, .2);
                    box-shadow: 0 2px 5px 2px rgba(185, 185, 185, .2);
                .coat{
                    color: #666666;
                    font-size: 13px;
                    padding: 5px 0;
                    span{
                        color: #333333;
                        font-size: 14px;
                    }
                }
            }
            .steps{
                width: 92%;
                margin: 15px 4%;
                background-color: #fff;
                padding: 15px 0 25px 15px;
                border-radius: 10px;
                -webkit-box-shadow: 0 2px 5px 2px rgba(185, 185, 185, .2);
                box-shadow: 0 2px 5px 2px rgba(185, 185, 185, .2);
            }
        }
    }
</style>



